.component-button {
  display: inline-flex;
  width: 25%;
  flex: 1 0 auto;
}

.component-button.wide {
  width: 50%;
}

.component-button button {
  background-color: #f1c6df;
  border: 0;
  font-size: 5rem;
  font-family: Mfonts;
  margin: 0 1px 0 0;
  flex: 1 0 auto;
  padding: 0;
}

.component-button:last-child button {
  margin-right: 0;
}

.component-button.orange button {
  background-color: #f5923e;
  
  color: white;
}
.component-button.pink button {
  background-color: #e26595;
  
  color: white;
}
.component-button.green button {
  background-color: #6bdd67;
 
  color: white;
}
.component-button.blue button {
  background-color: #3547e9;
  
  color: white;
}
.component-button.red button {
  background-color: #f70e0e;
  
  color: white;
}
.component-button.yellow button {
  background-color: #ece24b;
  font-size: 4rem;
  
  color: rgb(241, 10, 10);
}
.component-button.purple button {
  background-color: #9012c2;
  
  color: white;
}
.component-button.turquoise button {
  background-color: #5fdbc7;
  
  color: white;
}